<template>
  <div class="line">
    <span class="label">{{label}}</span>
    <span class="text">{{text}}</span>
  </div>
</template>

<script>
  export default {
    name: "TeamHeadInfo",
    props: ['label', 'text']
  }
</script>

<style scoped>
  .line {
    margin: 1rem 0;
    text-align: left;
    font-weight: 700;
    font-size: 1.55rem;
  }

  .label {
    font-weight: 400;
    margin-right: 1rem;
  }

  @media (max-width: 768px) {
    .line {
      margin: .5rem;
      font-size: 1rem
    }
    .label {
      margin-right: .3rem;

    }
  }
</style>



