<template>
  <router-link :to="'/player/'+player.id+'/'+player.name" :event="disabled ? '' : 'click'" v-if="player">
    <div class="team-player" :class="[reverse?'flex-reverse':'',noBorder?'no-border':'']">
      <div class="team-player-img" :style="`background-image: url(${player.headshot})`"></div>
      <div class="team-player-info" :class="[reverse?'team-player-info-reverse':'']">
        <div style="display: flex;align-items: center" :class="[reverse?'flex-reverse':'']">
          <span class="team-player-name">{{player.name}}</span>
          <span class="team-player-role" :class="[reverse?'team-player-role-reverse':'']">
        <svg viewBox="0 0 64 64" width="70%" height="70%" v-if="player.role==='support'">
          <path
            d="M51.9,23.2H40.8V12.1A4.1,4.1,0,0,0,36.7,8H27.3a4.1,4.1,0,0,0-4.1,4.1V23.2H12.1A4.1,4.1,0,0,0,8,27.3v9.4a4.1,4.1,0,0,0,4.1,4.1H23.2V51.9A4.1,4.1,0,0,0,27.3,56h9.4a4.1,4.1,0,0,0,4.1-4.1V40.8H51.9A4.1,4.1,0,0,0,56,36.7V27.3A4.1,4.1,0,0,0,51.9,23.2Z"></path>
        </svg>
        <svg viewBox="0 0 64 64" width="70%" height="70%" v-if="player.role==='offense'">
          <rect x="12" y="49.3" width="10.2" height="5.61"></rect>
          <path
            d="M22.2,19.1a10.2,10.2,0,0,0,0-1c-.8-6.9-5.1-9-5.1-9s-4.3,2.1-5.1,9c0,.3,0,1,0,1V45.4H22.2Z"></path>
          <rect x="26.9" y="49.3" width="10.2" height="5.61"></rect>
          <path
            d="M37.1,19.1a10.2,10.2,0,0,0,0-1C36.3,11.2,32,9,32,9s-4.3,2.1-5.1,9c0,.3,0,1,0,1V45.4H37.1Z"></path>
          <rect x="41.8" y="49.3" width="10.2" height="5.61"></rect>
          <path d="M52,19.1s0-.8,0-1c-.8-6.9-5.1-9-5.1-9s-4.3,2.1-5.1,9c0,.3,0,1,0,1V45.4H52Z"></path>
        </svg>
        <svg viewBox="0 0 64 64" width="70%" height="70%" v-if="player.role==='tank'">
          <path
            d="M51.4,24.1c0,3.1,0,6.2,0,9.3a4.7,4.7,0,0,1-.6,2.4A57.2,57.2,0,0,1,33.2,55.5a1.8,1.8,0,0,1-2.4,0A57.4,57.4,0,0,1,13.2,36a5.5,5.5,0,0,1-.7-2.8c0-5.8.1-11.7,0-17.5-.1-4.2,3.2-4.9,6.1-5.6A59.4,59.4,0,0,1,32.9,8C37.5,8,44.5,9.6,47,10.4s4.1,1.4,4.3,3.3.1,3.2.1,4.9,0,3.7,0,5.5Z"></path>
        </svg>
      </span>
          <div class="team-player-number">
            {{player.number}}
          </div>
        </div>

        <div :class="[reverse?'flex-end':'']">{{player.fullName}}</div>
        <div style="display: flex;align-items: center;font-weight: 300;font-size: 1rem"
             :class="[reverse?'flex-reverse':'']">
          <div style="width: 1.5rem;height:  1.5rem">
            <svg id="icon-home" viewBox="0 0 64 64" width="100%" height="100%">
              <title>Home</title>
              <g>
                <path class="cls-1"
                      d="M59.32,29.9l-25-20.95c-1.81-1.81-2.72-2-4.71,0L4.68,29.9a1.91,1.91,0,0,0-.23,2.68L5.67,34a1.91,1.91,0,0,0,2.68.23L31.2,15.11a1.25,1.25,0,0,1,1.61,0L55.65,34.28A1.91,1.91,0,0,0,58.33,34l1.22-1.46A1.91,1.91,0,0,0,59.32,29.9Z"></path>
                <path class="cls-1"
                      d="M32.56,20.78a.88.88,0,0,0-1.13,0l-18.77,16A1.91,1.91,0,0,0,12,38.21V52.57a1.91,1.91,0,0,0,1.91,1.91H24.38a1.91,1.91,0,0,0,1.91-1.91V39.4H37.72V52.57a1.91,1.91,0,0,0,1.91,1.91H50.1A1.91,1.91,0,0,0,52,52.57V38.21a1.91,1.91,0,0,0-.67-1.45Z"></path>
              </g>
            </svg>
          </div>
          <span>{{player.homeLocation}}</span>

        </div>

      </div>
      <slot name="suffix"></slot>
    </div>
  </router-link>
</template>

<script>
  export default {
    name: "PlayerBrief",
    props: ['player', 'reverse', 'noBorder', 'disabled']
  }
</script>

<style scoped>
  .team-player {
    padding: 0.1rem 1rem;
    text-align: left;
    font-size: 1.2rem;
    background-color: white;
    border: 1px solid rgba(51, 51, 51, .2);
    display: flex;
    align-items: flex-end;
  }

  .no-border {
    border: none;
    background-color: #f1f1f1;
  }

  .team-player-info {
    margin-left: 3rem;

  }

  .team-player-img {
    width: 120px;
    height: 120px;
    background-size: contain;
    background-repeat: no-repeat;
    flex-shrink: 0;
  }

  .team-player-number {
    color: black;
    width: 2rem;
    height: 2rem;
    background-color: rgb(222, 225, 230);
    display: flex;
    align-items: center;
    justify-content: center
  }

  .team-player-role {
    margin-left: 1rem;
    fill: white;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;

  }

  .team-player-name {
    font-weight: 700;
    font-size: 1.5rem
  }

  .team-player-info-reverse {
    margin-right: 3rem;
    margin-left: 0;
  }

  .team-player-role-reverse {
    margin-left: 0;
    margin-right: 1rem;
  }

  .flex-reverse {
    flex-direction: row-reverse;
  }

  .flex-end {
    display: flex;
    justify-content: flex-end;
  }

  @media (max-width: 768px) {
    .team-player {
      padding: 0.1rem 0.3rem;

    }

    .team-player-img {
      width: 75px;
      height: 75px;
      background-size: contain;
    }

    .team-player-name, .team-player {
      font-size: 1rem;
    }

    .team-player-number, .team-player-role {
      width: 1.2rem;
      height: 1.2rem;
      font-size: .8rem;
    }

    .team-player-info {
      margin-left: 1rem;
    }
  }
</style>



